<template>
  <div class="register-page">
    <div class="left">
      <img src="../assets/dark.png" alt="">
      <div class="route">
        <router-link to="/register" class="btn btn-success register" id="button">Register</router-link>
      </div>
    </div>
    <div class="vertical-line"></div>
    <div class="right">
      <form class="row g-3" @submit.prevent="login">
        <h2 class="mb-4">Login Form</h2>
        <div class="">
          <label for="email" class="form-label">Email</label>
          <input type="email" v-model="email" class="form-control thick-border" id="inputEmail">
        </div>
        <div class="">
          <label for="password" class="form-label">Password</label>
          <input type="password" v-model="password" class="form-control thick-border" id="inputPassword">
        </div>
        <div class="col-12">
          <button type="submit" class="btn btn-primary">Login</button>
        </div>
      </form>
    </div>
  </div>
</template>

<style scoped>
@import url('../assets/Login.css');
</style>

<script>
import axios from 'axios';
import Cookies from 'js-cookie';
export default {
  data() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    login() {
      // Validate email and password before making the request
      if (!this.email || !this.password) {
        // Handle validation error, e.g., show an error message
        console.error('Email and password are required');
        return;
      }

      // Make the login request to the server
      axios.post('http://localhost:3000/login', { email: this.email, password: this.password })
        .then(response => {
          if (response.data.success) {
            // Login successful
            const role = response.data.role;
            const token = response.data.token;

            // Store the token in localStorage
            Cookies.set('token', token, { expires: 1 / 24 });
            this.$store.commit('setLoggedIn', true);

            // Redirect based on the user's role
            switch (role) {
              case 'member':
                this.$router.push('/member-home');
                break;
              case 'employee':
                this.$router.push('/employee-home');
                break;
              case 'owner':
                this.$router.push('/owner-home');
                break;
              default:
                // Handle unknown role or unexpected response
                console.error('Unknown role:', role);
                break;
            }
          } else {
            // Handle unsuccessful login, e.g., show an error message
            console.error('Login failed:', response.data.message);
          }
        })
        .catch(error => {
          // Handle any network or server error
          console.error('Login error:', error);
        });
    }
  }
};
</script>