<template>
  <nav class="navbar bg-body-tertiary bg-dark" data-bs-theme="dark">
    <div class="container-fluid d-flex justify-content-between align-items-center">
      <a class="navbar-brand" href="#">
        <img src="../assets/logo-light.png" alt="Logo" width="30" height="24" class="d-inline-block align-text-top">
        KSD NGO FOUNDATION
      </a>
      <div class="navbar-profile">
        <img src="../assets/profile.png" alt="" width="40">
        <!-- Show LOGIN or LOGOUT based on the user's login status -->
        <router-link v-if="!loggedIn" to="/login" class="btn btn-secondary btn-sm mx-3">LOGIN</router-link>
        <button v-else class="btn btn-secondary btn-sm mx-3" @click="logout">LOGOUT</button>
      </div>
    </div>
  </nav>
</template>

<style scoped>
@import url("../assets/Navbar.css");
</style>

<script>
import Cookies from 'js-cookie';
import axios from 'axios';

export default {
  computed: {
    loggedIn() {
      // Access the loggedIn state directly from Vuex
      return this.$store.state.loggedIn;
    },
  },
  methods: {
    async logout() {
      try{
      const token = Cookies.get('token');
      await axios.post('http://localhost:3000/api/logout', {}, {
          headers: {
            Authorization: token,
          },
        });
      Cookies.remove('token');
      this.$store.commit('setLoggedIn', false);
      this.$router.push('/login');
      } catch (error){
        console.error('Logout Error', error.message);
      }
    },
  },
};
</script>