<template>
  <div class="container">
    <h1>Buy Blood</h1>
    <div class="button-container">
      <button v-for="group in bloodGroups" :key="group" @click="handleButtonClick(group)"
        :class="{ 'active-button': selectedBloodGroup === group }" class="blood-group-button">
        {{ group }}
      </button>
    </div>

    <!-- Conditionally render the input based on button click -->
    <div v-if="showInput" class="input-container">
      <label for="bloodUnits">Enter Units of Blood:</label>
      <input v-model="units" type="number" id="bloodUnits" />

      <div>
        <label>
          <input type="checkbox" v-model="deliveryOption" /> Delivery
        </label>
      </div>

      <!-- Conditionally render the document upload input for delivery option -->
      <div v-if="deliveryOption">
        <label for="proofDocument">Upload Document of Proof:</label>
        <input type="file" id="proofDocument" />
      </div>

      <button @click="buyBlood" class="buybtn">Buy</button>
    </div>
  </div>
</template>
  
<style scoped>
  .container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      height: 100vh;
  }

  .button-container {
      display: flex;
      align-items: center;
      justify-content: center;
  }

  .blood-group-button {
      width: 70px;
      height: 50px;
      margin: 5px;
      padding: 10px;
      cursor: pointer;
  }

  .active-button {
      background-color: #3498db;
      color: #fff;
  }

  .input-container {
      margin-top: 20px;
  }

  .buybtn {
    margin-top: 10px;
    cursor: pointer;
  }
</style>
  
  
<script>
import Swal from 'sweetalert2';

export default {
  name: 'BuyBlood',
  data() {
    return {
      showInput: false,
      selectedBloodGroup: '',
      units: 0,
      deliveryOption: false, // Added deliveryOption state
      bloodGroups: ['A+', 'A-', 'B+', 'B-', 'AB+', 'AB-', 'O+', 'O-']
    };
  },
  methods: {
    handleButtonClick(bloodGroup) {
      this.selectedBloodGroup = bloodGroup;
      this.showInput = true;
    },
    buyBlood() {
      // Handle buying blood with the selected blood group, units, and delivery option
      console.log(
        `Buying ${this.units} units of blood with ${this.selectedBloodGroup} group. Delivery option: ${this.deliveryOption ? 'Yes' : 'No'}`
      );

      // Show popup message based on the delivery option
      if (this.deliveryOption) {
        Swal.fire({
          title: 'Order Confirmation',
          text: 'Your document will be verified, and then your order will be confirmed.',
          icon: 'success'
        });
      } else {
        Swal.fire({
          title: 'Order Confirmation',
          text: 'Please bring your documents while collecting blood.',
          icon: 'success'
        });
      }

      // Reset state
      this.showInput = false;
      this.selectedBloodGroup = '';
      this.units = 0;
      this.deliveryOption = false;
    }
  }
};
</script>