<template>
    <div class="main">
        <div class="tests-container">
        <!-- Toggle buttons for blood tests -->
        <div v-for="(test, index) in bloodTests" :key="index" class="test-item">
            <button :class="['btn', 'btn-outline-secondary', { 'selected': selectedTests.includes(test.id) }]"
                @click="toggleTest(test.id)">
                {{ test.name }}
            </button>
        </div>
    </div>
        <!-- Button to book a blood test slot -->
        <button @click="bookBloodTest" class="book-button">Book Blood Test</button>
    </div>
</template>

<style scoped>
.main {
    max-width: 800px;
    margin: auto;
    text-align: center;
}

.tests-container {
    margin-top: 30px;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 25px;
}

.test-item {
    text-align: center;
}

.btn {
    width: 200px;
    height: 80px;
    padding: 8px 16px;
    cursor: pointer;
}

.btn-outline-secondary {
    background-color: #fff;
    border: 1px solid #ccc;
    color: #333;
}

.selected {
    background-color: #007bff;
    color: #fff;
    border-color: #007bff;
}

.book-button {
    display: inline-block;
    border-radius: 10px;
    margin-top: 16px;
    padding: 10px;
    background-color: #28a745;
    color: #fff;
    border: none;
    cursor: pointer;
}
</style>

<script>
export default {
    name: 'BloodTest',
    data() {
        return {
            bloodTests: [
                { id: 1, name: 'Complete Blood Count (CBC)' },
                { id: 2, name: 'Blood Glucose Test' },
                { id: 3, name: 'Cholesterol Panel' },
                { id: 4, name: 'Hemoglobin A1c' },
                { id: 5, name: 'Thyroid Function Tests' },
                { id: 6, name: 'Liver Function Tests' },
                { id: 7, name: 'Renal Function Tests' },
                { id: 8, name: 'Lipid Profile' },
                { id: 9, name: 'Iron Studies' },
                { id: 10, name: 'Prothrombin Time (PT)' },
            ],
            selectedTests: []
        };
    },
    methods: {
        toggleTest(testId) {
            // Toggle the selected state of a blood test
            if (this.selectedTests.includes(testId)) {
                this.selectedTests = this.selectedTests.filter(id => id !== testId);
            } else {
                this.selectedTests.push(testId);
            }
        },
        bookBloodTest() {
            // Handle the logic for booking the blood test slot
            console.log('Booking blood test with selected tests:', this.selectedTests);
            // You can add your booking logic here
            this.$router.push('/book-slot');
        }
    }
};
</script>