<script>
import axios from 'axios';

export default {
    data() {
        return {
            formData: {
                name: '',
                email: '',
                phone: '',
                message: '',
            },
            isFormDisabled: false,
            isEmailValid: true,
            formFields: {
                name: 'Name',
                email: 'Email',
                phone: 'Phone',
                message: 'Message',
            },
        };
    },
    methods: {
        fieldIsRequired(fieldName) {
            return this.formData[fieldName].trim() === '' && this.isFormDisabled;
        },
        async handleFormSubmit(event) {
            event.preventDefault();

            // Check if all fields are filled
            const isFormValid = Object.values(this.formData).every(value => value.trim() !== '');

            if (!isFormValid) {
                // Display an error message or handle invalid form
                console.error('Please fill in all required fields.');
                this.isFormDisabled = true;
                setTimeout(() => {
                    this.isFormDisabled = false;
                }, 5000); // Adjust the timeout as needed
                return;
            }

            const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
            this.isEmailValid = emailRegex.test(this.formData.email);

            if (!this.isEmailValid) {
                // Display an error message for invalid email format
                console.error('Please enter a valid email address.');
                this.isFormDisabled = true;
                // Reset the flag after displaying the error
                setTimeout(() => {
                    this.isFormDisabled = false;
                }, 5000); // Adjust the timeout as needed
                return;
            }
            try {
                // Send form data to the backend
                const response = await axios.post('http://localhost:3000/submit-form/contact', this.formData);

                // Handle the response from the server
                if (response.data.success) {
                    // Reset the form or perform any other actions on successful submission
                    console.log('Form submitted successfully!');
                    this.formData = {
                        name: '',
                        email: '',
                        phone: '',
                        message: '',
                    };
                } else {
                    // Handle errors from the server
                    console.error('Error submitting form:', response.data.message);
                }
            } catch (error) {
                // Handle network errors or other issues
                console.error('Error:', error.message);
            }
        },
    },
};
</script>